.Cards {
  display: block;
  /* flex-direction: column;
    justify-content: center; */
  align-items: center;
  text-decoration: none;
  overflow: hidden;
  border: none;
  background-color: var(--background-color-cards);
  border-radius: 12px;
  padding: 0px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
.image {
  width: 100%;
}
.texto {
  font-size: 1em;
  margin: 0 0 0.3em;
  color: var(--text-color-card);
}

.Cards:hover {
  background-color: var(--background-color-cards-hover);
  cursor: pointer;
  border-radius: 15px;
  box-shadow: 0px 0px 70px var(--background-color-shadow); 
  
}
